//
//BasicBootstraptable
//

.table{
--#{$variable-prefix}table-bg:#{$table-bg};
--#{$variable-prefix}table-accent-bg:#{$table-accent-bg};
--#{$variable-prefix}table-striped-color:#{$table-striped-color};
--#{$variable-prefix}table-striped-bg:#{$table-striped-bg};
--#{$variable-prefix}table-active-color:#{$table-active-color};
--#{$variable-prefix}table-active-bg:#{$table-active-bg};
--#{$variable-prefix}table-hover-color:#{$table-hover-color};
--#{$variable-prefix}table-hover-bg:#{$table-hover-bg};

width:100%;
margin-bottom:$spacer;
color:$table-color;
vertical-align:$table-cell-vertical-align;
border-color:$table-border-color;

//Targetth&td
//Weneedthechildcombinatortopreventstylesleakingtonestedtableswhichdoesn'thavea`.table`class.
//Weusetheuniversalselectorsheretosimplifytheselector(elsewewouldneed6differentselectors).
//Anotheradvantageisthatthisgenerateslesscodeandmakestheselectorlessspecificmakingiteasiertooverride.
//stylelint-disable-next-lineselector-max-universal
>:not(caption)>*>*{
padding:$table-cell-padding-y$table-cell-padding-x;
background-color:var(--#{$variable-prefix}table-bg);
border-bottom-width:$table-border-width;
box-shadow:inset0009999pxvar(--#{$variable-prefix}table-accent-bg);
}

>tbody{
vertical-align:inherit;
}

>thead{
vertical-align:bottom;
}

//Highlightbordercolorbetweenthead,tbodyandtfoot.
>:not(:last-child)>:last-child>*{
border-bottom-color:$table-group-separator-color;
}
}


//
//Changeplacementofcaptionswithaclass
//

.caption-top{
caption-side:top;
}


//
//Condensedtablew/halfpadding
//

.table-sm{
//stylelint-disable-next-lineselector-max-universal
>:not(caption)>*>*{
padding:$table-cell-padding-y-sm$table-cell-padding-x-sm;
}
}


//Borderversions
//
//Addorremovebordersallaroundthetableandbetweenallthecolumns.
//
//Whenbordersareaddedonallsidesofthecells,thecornerscanrenderoddwhen
//thesebordersdonothavethesamecolororiftheyaresemi-transparent.
//Thereforweaddtopandborderbottomstothe`tr`sandleftandrightborders
//tothe`td`sor`th`s

.table-bordered{
>:not(caption)>*{
border-width:$table-border-width0;

//stylelint-disable-next-lineselector-max-universal
>*{
border-width:0$table-border-width;
}
}
}

.table-borderless{
//stylelint-disable-next-lineselector-max-universal
>:not(caption)>*>*{
border-bottom-width:0;
}
}

//Zebra-striping
//
//Defaultzebra-stripestyles(alternatinggrayandtransparentbackgrounds)

.table-striped{
>tbody>tr:nth-of-type(#{$table-striped-order}){
--#{$variable-prefix}table-accent-bg:var(--#{$variable-prefix}table-striped-bg);
color:var(--#{$variable-prefix}table-striped-color);
}
}

//Activetable
//
//The`.table-active`classcanbeaddedtohighlightrowsorcells

.table-active{
--#{$variable-prefix}table-accent-bg:var(--#{$variable-prefix}table-active-bg);
color:var(--#{$variable-prefix}table-active-color);
}

//Hovereffect
//
//Placedheresinceithastocomeafterthepotentialzebrastriping

.table-hover{
>tbody>tr:hover{
--#{$variable-prefix}table-accent-bg:var(--#{$variable-prefix}table-hover-bg);
color:var(--#{$variable-prefix}table-hover-color);
}
}


//Tablevariants
//
//Tablevariantssetthetablecellbackgrounds,bordercolors
//andthecolorsofthestriped,hovered&activetables

@each$color,$valuein$table-variants{
@includetable-variant($color,$value);
}

//Responsivetables
//
//Generateseriesof`.table-responsive-*`classesforconfiguringthescreen
//sizeofwhereyourtablewilloverflow.

@each$breakpointinmap-keys($grid-breakpoints){
$infix:breakpoint-infix($breakpoint,$grid-breakpoints);

@includemedia-breakpoint-down($breakpoint){
.table-responsive#{$infix}{
overflow-x:auto;
-webkit-overflow-scrolling:touch;
}
}
}
