//Breakpointviewportsizesandmediaqueries.
//
//Breakpointsaredefinedasamapof(name:minimumwidth),orderfromsmalltolarge:
//
//(xs:0,sm:576px,md:768px,lg:992px,xl:1200px)
//
//Themapdefinedinthe`$grid-breakpoints`globalvariableisusedasthe`$breakpoints`argumentbydefault.

//Nameofthenextbreakpoint,ornullforthelastbreakpoint.
//
//>>breakpoint-next(sm)
//md
//>>breakpoint-next(sm,(xs:0,sm:576px,md:768px,lg:992px,xl:1200px))
//md
//>>breakpoint-next(sm,$breakpoint-names:(xssmmdlgxl))
//md
@functionbreakpoint-next($name,$breakpoints:$grid-breakpoints,$breakpoint-names:map-keys($breakpoints)){
$n:index($breakpoint-names,$name);
@ifnot$n{
@error"breakpoint`#{$name}`notfoundin`#{$breakpoints}`";
}
@returnif($n<length($breakpoint-names),nth($breakpoint-names,$n+1),null);
}

//Minimumbreakpointwidth.Nullforthesmallest(first)breakpoint.
//
//>>breakpoint-min(sm,(xs:0,sm:576px,md:768px,lg:992px,xl:1200px))
//576px
@functionbreakpoint-min($name,$breakpoints:$grid-breakpoints){
$min:map-get($breakpoints,$name);
@returnif($min!=0,$min,null);
}

//Maximumbreakpointwidth.
//Themaximumvalueisreducedby0.02pxtoworkaroundthelimitationsof
//`min-`and`max-`prefixesandviewportswithfractionalwidths.
//Seehttps://www.w3.org/TR/mediaqueries-4/#mq-min-max
//Uses0.02pxratherthan0.01pxtoworkaroundacurrentroundingbuginSafari.
//Seehttps://bugs.webkit.org/show_bug.cgi?id=178261
//
//>>breakpoint-max(md,(xs:0,sm:576px,md:768px,lg:992px,xl:1200px))
//767.98px
@functionbreakpoint-max($name,$breakpoints:$grid-breakpoints){
$max:map-get($breakpoints,$name);
@returnif($maxand$max>0,$max-.02,null);
}

//Returnsablankstringifsmallestbreakpoint,otherwisereturnsthenamewithadashinfront.
//Usefulformakingresponsiveutilities.
//
//>>breakpoint-infix(xs,(xs:0,sm:576px,md:768px,lg:992px,xl:1200px))
//""(Returnsablankstring)
//>>breakpoint-infix(sm,(xs:0,sm:576px,md:768px,lg:992px,xl:1200px))
//"-sm"
@functionbreakpoint-infix($name,$breakpoints:$grid-breakpoints){
@returnif(breakpoint-min($name,$breakpoints)==null,"","-#{$name}");
}

//Mediaofatleasttheminimumbreakpointwidth.Noqueryforthesmallestbreakpoint.
//Makesthe@contentapplytothegivenbreakpointandwider.
@mixinmedia-breakpoint-up($name,$breakpoints:$grid-breakpoints){
$min:breakpoint-min($name,$breakpoints);
@if$min{
@media(min-width:$min){
@content;
}
}@else{
@content;
}
}

//Mediaofatmostthemaximumbreakpointwidth.Noqueryforthelargestbreakpoint.
//Makesthe@contentapplytothegivenbreakpointandnarrower.
@mixinmedia-breakpoint-down($name,$breakpoints:$grid-breakpoints){
$max:breakpoint-max($name,$breakpoints);
@if$max{
@media(max-width:$max){
@content;
}
}@else{
@content;
}
}

//Mediathatspansmultiplebreakpointwidths.
//Makesthe@contentapplybetweentheminandmaxbreakpoints
@mixinmedia-breakpoint-between($lower,$upper,$breakpoints:$grid-breakpoints){
$min:breakpoint-min($lower,$breakpoints);
$max:breakpoint-max($upper,$breakpoints);

@if$min!=nulland$max!=null{
@media(min-width:$min)and(max-width:$max){
@content;
}
}@elseif$max==null{
@includemedia-breakpoint-up($lower,$breakpoints){
@content;
}
}@elseif$min==null{
@includemedia-breakpoint-down($upper,$breakpoints){
@content;
}
}
}

//Mediabetweenthebreakpoint'sminimumandmaximumwidths.
//Nominimumforthesmallestbreakpoint,andnomaximumforthelargestone.
//Makesthe@contentapplyonlytothegivenbreakpoint,notviewportsanywiderornarrower.
@mixinmedia-breakpoint-only($name,$breakpoints:$grid-breakpoints){
$min:breakpoint-min($name,$breakpoints);
$next:breakpoint-next($name,$breakpoints);
$max:breakpoint-max($next);

@if$min!=nulland$max!=null{
@media(min-width:$min)and(max-width:$max){
@content;
}
}@elseif$max==null{
@includemedia-breakpoint-up($name,$breakpoints){
@content;
}
}@elseif$min==null{
@includemedia-breakpoint-down($next,$breakpoints){
@content;
}
}
}
