//Utilitygenerator
//Usedtogenerateutilities&printutilities
@mixingenerate-utility($utility,$infix,$is-rfs-media-query:false){
$values:map-get($utility,values);

//Ifthevaluesarealistorstring,convertitintoamap
@iftype-of($values)=="string"ortype-of(nth($values,1))!="list"{
$values:zip($values,$values);
}

@each$key,$valuein$values{
$properties:map-get($utility,property);

//Multiplepropertiesarepossible,forexamplewithverticalorhorizontalmarginsorpaddings
@iftype-of($properties)=="string"{
$properties:append((),$properties);
}

//Usecustomclassifpresent
$property-class:if(map-has-key($utility,class),map-get($utility,class),nth($properties,1));
$property-class:if($property-class==null,"",$property-class);

//Stateparamstogeneratepseudo-classes
$state:if(map-has-key($utility,state),map-get($utility,state),());

$infix:if($property-class==""andstr-slice($infix,1,1)=="-",str-slice($infix,2),$infix);

//Don'tprefixifvaluekeyisnull(eg.withshadowclass)
$property-class-modifier:if($key,if($property-class==""and$infix=="","","-")+$key,"");

@ifmap-get($utility,rfs){
//Insidethemediaquery
@if$is-rfs-media-query{
$val:rfs-value($value);

//Donotrenderanythingiffluidandnonfluidvaluesarethesame
$value:if($val==rfs-fluid-value($value),null,$val);
}
@else{
$value:rfs-fluid-value($value);
}
}

$is-css-var:map-get($utility,css-var);
$is-local-vars:map-get($utility,local-vars);
$is-rtl:map-get($utility,rtl);

@if$value!=null{
@if$is-rtl==false{
/*rtl:begin:remove*/
}

@if$is-css-var{
.#{$property-class+$infix+$property-class-modifier}{
--#{$variable-prefix}#{$property-class}:#{$value};
}

@each$pseudoin$state{
.#{$property-class+$infix+$property-class-modifier}-#{$pseudo}:#{$pseudo}{
--#{$variable-prefix}#{$property-class}:#{$value};
}
}
}@else{
.#{$property-class+$infix+$property-class-modifier}{
@each$propertyin$properties{
@if$is-local-vars{
@each$local-var,$valuein$is-local-vars{
--#{$variable-prefix}#{$local-var}:#{$value};
}
}
#{$property}:$valueif($enable-important-utilities,!important,null);
}
}

@each$pseudoin$state{
.#{$property-class+$infix+$property-class-modifier}-#{$pseudo}:#{$pseudo}{
@each$propertyin$properties{
#{$property}:$valueif($enable-important-utilities,!important,null);
}
}
}
}

@if$is-rtl==false{
/*rtl:end:remove*/
}
}
}
}
