//Gridsystem
//
//Generatesemanticgridcolumnswiththesemixins.

@mixinmake-row($gutter:$grid-gutter-width){
--#{$variable-prefix}gutter-x:#{$gutter};
--#{$variable-prefix}gutter-y:0;
display:flex;
flex-wrap:wrap;
margin-top:calc(var(--#{$variable-prefix}gutter-y)*-1);//stylelint-disable-linefunction-disallowed-list
margin-right:calc(var(--#{$variable-prefix}gutter-x)*-.5);//stylelint-disable-linefunction-disallowed-list
margin-left:calc(var(--#{$variable-prefix}gutter-x)*-.5);//stylelint-disable-linefunction-disallowed-list
}

@mixinmake-col-ready($gutter:$grid-gutter-width){
//Addboxsizingifonlythegridisloaded
box-sizing:if(variable-exists(include-column-box-sizing)and$include-column-box-sizing,border-box,null);
//Preventcolumnsfrombecomingtoonarrowwhenatsmallergridtiersby
//alwayssetting`width:100%;`.Thisworksbecausewesetthewidth
//laterontooverridethisinitialwidth.
flex-shrink:0;
width:100%;
max-width:100%;//Prevent`.col-auto`,`.col`(&responsivevariants)frombreakingoutthegrid
padding-right:calc(var(--#{$variable-prefix}gutter-x)*.5);//stylelint-disable-linefunction-disallowed-list
padding-left:calc(var(--#{$variable-prefix}gutter-x)*.5);//stylelint-disable-linefunction-disallowed-list
margin-top:var(--#{$variable-prefix}gutter-y);
}

@mixinmake-col($size:false,$columns:$grid-columns){
@if$size{
flex:00auto;
width:percentage(divide($size,$columns));

}@else{
flex:110;
max-width:100%;
}
}

@mixinmake-col-auto(){
flex:00auto;
width:auto;
}

@mixinmake-col-offset($size,$columns:$grid-columns){
$num:divide($size,$columns);
margin-left:if($num==0,0,percentage($num));
}

//Rowcolumns
//
//Specifyonaparentelement(e.g.,.row)toforceimmediatechildrenintoNN
//numberofcolumns.Supportswrappingtonewlines,butdoesnotdoaMasonry
//stylegrid.
@mixinrow-cols($count){
>*{
flex:00auto;
width:divide(100%,$count);
}
}

//Frameworkgridgeneration
//
//UsedonlybyBootstraptogeneratethecorrectnumberofgridclassesgiven
//anyvalueof`$grid-columns`.

@mixinmake-grid-columns($columns:$grid-columns,$gutter:$grid-gutter-width,$breakpoints:$grid-breakpoints){
@each$breakpointinmap-keys($breakpoints){
$infix:breakpoint-infix($breakpoint,$breakpoints);

@includemedia-breakpoint-up($breakpoint,$breakpoints){
//Providebasic`.col-{bp}`classesforequal-widthflexboxcolumns
.col#{$infix}{
flex:100%;//Flexbugs#4:https://github.com/philipwalton/flexbugs#flexbug-4
}

.row-cols#{$infix}-auto>*{
@includemake-col-auto();
}

@if$grid-row-columns>0{
@for$ifrom1through$grid-row-columns{
.row-cols#{$infix}-#{$i}{
@includerow-cols($i);
}
}
}

.col#{$infix}-auto{
@includemake-col-auto();
}

@if$columns>0{
@for$ifrom1through$columns{
.col#{$infix}-#{$i}{
@includemake-col($i,$columns);
}
}

//`$columns-1`becauseoffsettingbythewidthofanentirerowisn'tpossible
@for$ifrom0through($columns-1){
@ifnot($infix==""and$i==0){//Avoidemittinguseless.offset-0
.offset#{$infix}-#{$i}{
@includemake-col-offset($i,$columns);
}
}
}
}

//Gutters
//
//Makeuseof`.g-*`,`.gx-*`or`.gy-*`utilitiestochangespacingbetweenthecolumns.
@each$key,$valuein$gutters{
.g#{$infix}-#{$key},
.gx#{$infix}-#{$key}{
--#{$variable-prefix}gutter-x:#{$value};
}

.g#{$infix}-#{$key},
.gy#{$infix}-#{$key}{
--#{$variable-prefix}gutter-y:#{$value};
}
}
}
}
}

@mixinmake-cssgrid($columns:$grid-columns,$breakpoints:$grid-breakpoints){
@each$breakpointinmap-keys($breakpoints){
$infix:breakpoint-infix($breakpoint,$breakpoints);

@includemedia-breakpoint-up($breakpoint,$breakpoints){
@if$columns>0{
@for$ifrom1through$columns{
.g-col#{$infix}-#{$i}{
grid-column:auto/span$i;
}
}

//Startwith`1`because`0`isandinvalidvalue.
//Endswith`$columns-1`becauseoffsettingbythewidthofanentirerowisn'tpossible.
@for$ifrom1through($columns-1){
.g-start#{$infix}-#{$i}{
grid-column-start:$i;
}
}
}
}
}
}
