//Containerwidths
//
//Setthecontainerwidth,andoverrideitforfixednavbarsinmediaqueries.

@if$enable-grid-classes{
//Singlecontainerclasswithbreakpointmax-widths
.container,
//100%widecontaineratallbreakpoints
.container-fluid{
@includemake-container();
}

//Responsivecontainersthatare100%wideuntilabreakpoint
@each$breakpoint,$container-max-widthin$container-max-widths{
.container-#{$breakpoint}{
@extend.container-fluid;
}

@includemedia-breakpoint-up($breakpoint,$grid-breakpoints){
%responsive-container-#{$breakpoint}{
max-width:$container-max-width;
}

//Extendeachbreakpointwhichissmallerorequaltothecurrentbreakpoint
$extend-breakpoint:true;

@each$name,$widthin$grid-breakpoints{
@if($extend-breakpoint){
.container#{breakpoint-infix($name,$grid-breakpoints)}{
@extend%responsive-container-#{$breakpoint};
}

//Oncethecurrentbreakpointisreached,stopextending
@if($breakpoint==$name){
$extend-breakpoint:false;
}
}
}
}
}
}
